import styled from "@emotion/styled";

const AbilityText = styled.div`
  white-space: pre
`;

const parseElement = id => [null, "", "", "", "", ""][id] ?? "??";
const parseRace = id => [null, "", "", "", "", "",null ,null ,"",null ,""][id] ?? "??";

const parseAbility = (id, [arg0, arg1]) => {
  switch(id) {
    case 20001:
      return `\n \n     ${arg0} ${parseRace(arg1)}`;
    case 20002:
      return `\n  ${arg0}%  ${arg1} `;
    case 20003:
      return `\n  ${arg0}  (Combo)`;
    case 20004:
      return `\n\n \n    ${parseElement(arg0)}${parseRace(arg1)}`;
    case 20005:
      return `\n\n  CD -${arg0}`;
    case 20006:
      return `\n\n  EP +${arg0}`;
    case 20007:
      return `\n  ${arg0} `;
    case 20008:
      return `\n `;
    case 20009:
      return `\n ${parseElement(arg0)} +${arg1}1%`;
    case 20010:
      return `\n ${parseRace(arg0)} +${arg1}%`;
    case 20011:
      return `\n `;
    case 20012:
      return `\n `;
    case 20013:
      return `\n `;
    case 20014:
      return `\n `;
    case 20015:
      return `\n `;
    case 20016:
      return `\n `;
    case 20017:
      return `\n ${arg0}`;
    case 20018:
      return `\n `;
    case 20019:
      return `\n\n \n    ${parseElement(arg0)}${parseRace(arg1)}`;
    case 20020:
      return `\n \n    `;
    case 20021:
      return `\n  ${arg0}% \n    `;
    case 20022:
      return `\n `;
    case 20023:
      return `\n `;
    default:
      return ` #${id}\n`;
  }
}

const BondAbility = ({ability, args}) => {
  return (
    <AbilityText>{parseAbility(ability, args)}</AbilityText>
  );
}

export default BondAbility;