import styled from "@emotion/styled";
import { useOutletContext } from "react-router-dom";
import CardText from "./cardText";

const ConditionText = styled.div`
  font-family: Roboto;
  padding-bottom: 8px;
`;

const BondCondition = ({condition, conditionType, fullText}) => {
  const {cardData} = useOutletContext();
  switch(conditionType) {
    case -100:
      return (
        <ConditionText>
          
          <CardText id={condition} name={cardData[condition]?.name} />
        </ConditionText>
      );
    case 1:
      return fullText ? (
        <ConditionText>
          
          <CardText id={condition} name={cardData[condition]?.name} />
        </ConditionText>
      ) : (
        <ConditionText></ConditionText>
      );
    case 2:
      return fullText ? (
        <ConditionText>
           Dual Max
          <CardText id={condition} name={cardData[condition]?.name} />
        </ConditionText>
      ) : (
        <ConditionText> Dual Max</ConditionText>
      );
    case 3:
      return fullText ? (
        <ConditionText>
           All Max
          <CardText id={condition} name={cardData[condition]?.name} />
        </ConditionText>
      ) : (
        <ConditionText> All Max</ConditionText>
      );
    default:
      return (
        <ConditionText>
          
        </ConditionText>
      );
  }
}

export default BondCondition;